export function defaultFeeFor(kindObj) {
  if (!kindObj) return 0;
  if (typeof kindObj.min === 'number') return kindObj.min;
  if (Array.isArray(kindObj.range)) return kindObj.range[0];
  return 0;
}

/**
 *    DAMAGE_RULES    
 * @param {string} productCategory -     
 * @param {string} productName -   (  )
 * @returns {string} -    DAMAGE_RULES
 */
export function detectDamageCategory(productCategory, productName = '') {
  if (!productCategory) return '';
  
  const catLower = productCategory.toLowerCase();
  const nameLower = (productName || '').toLowerCase();
  
  //  
  const exactMatches = {
    '': '',
    '': '',
    '': '',
    '': '',
    '': '',
    '': '',
    '': '',
    '': '',
    '': '',
  };
  
  for (const [key, value] of Object.entries(exactMatches)) {
    if (catLower === key || catLower.includes(key)) {
      return value;
    }
  }
  
  //  -     
  if (catLower.includes('') || catLower.includes('')) {
    //    
    const heightMatch = nameLower.match(/h\s*[:=]?\s*(\d+)/i) || nameLower.match(/(\d+)\s*/);
    if (heightMatch) {
      const height = parseInt(heightMatch[1]);
      if (height <= 30) return ' (h:  30 )';
      if (height <= 100) return ' /  (h: 30   1 )';
      if (height <= 180) return ' (h: 1,2   1,8 )';
    }
    //   -  
    return ' /  (h: 30   1 )';
  }
  
  //  / 
  if (catLower.includes('') || catLower.includes('')) {
    return ' / ';
  }
  
  // 
  if (catLower.includes('')) {
    return '     ()';
  }
  
  // 
  if (catLower.includes('') || catLower.includes('') || catLower.includes('')) {
    return '  (, )';
  }
  
  // 
  if (catLower.includes('') || catLower.includes('') || catLower.includes('led')) {
    return ' ';
  }
  
  // 
  if (catLower.includes('')) {
    return '';
  }
  
  // 
  if (catLower.includes('')) {
    return '';
  }
  
  // 
  if (catLower.includes('')) {
    return '';
  }
  
  // 
  if (catLower.includes('')) {
    return '';
  }
  
  // 
  if (catLower.includes('')) {
    return '';
  }
  
  //  
  return '';
}
